.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
    position: relative;
    height: 800px;
}

.project-card img {
    width: 100%;
    height: 500px;
}

.project-title {
    color: #fff;
    padding: 1rem 0;
}

.project-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
    position: relative;
}

.pro-btns {
    position: absolute;
    bottom: 10px; /* Adjust the distance from the bottom */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Center horizontally */
    width: fit-content; /* Adjust button width based on content */
    text-align: center; /* Center the text within the button */
    margin-bottom: 15px; /* Add space between button and content */
}

.pro-btns .btn {
    padding: 0.5rem 1rem;
}

@media screen and (max-width: 740px) {
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}